import { useCursor, useGLTF } from '@react-three/drei';
import { Canvas, MeshProps } from '@react-three/fiber';
import * as React from 'react';
import { Mesh } from 'three';
import { proxy, useSnapshot } from 'valtio';
import { proxySet, useProxy } from 'valtio/utils';
import Area from './Area';
import glbCompressed from './compressed.glb';

const modes = ['translate', 'rotate', 'scale'];
const state = proxy<{ current: string | null; mode: number; models: Set<string> }>({
  current: null,
  mode: 0,
  models: proxySet(),
});

function Model({ name, ...props }: MeshProps & { name: string }) {
  // Ties this component to the state model
  const snap = useSnapshot(state);
  // Fetching the GLTF, nodes is a collection of all the meshes
  // It's cached/memoized, it only gets loaded and parsed once
  const { nodes } = useGLTF(glbCompressed);
  // Feed hover state into useCursor, which sets document.body.style.cursor to pointer|auto
  const [hovered, setHovered] = React.useState(false);
  useCursor(hovered);
  React.useEffect(() => {
    // console.log('Model.mounted', { name });
    state.models.add(name);
    return () => {
      state.models.delete(name);
    };
  }, []);
  // console.log('Model', { models: snap.models });
  return (
    <mesh
      // Click sets the mesh as the new target
      onClick={(e) => (e.stopPropagation(), (state.current = name))}
      // If a click happened but this mesh wasn't hit we null out the target,
      // This works because missed pointers fire before the actual hits
      onPointerMissed={(e) => e.type === 'click' && (state.current = null)}
      // Right click cycles through the transform modes
      onContextMenu={(e) =>
        snap.current === name && (e.stopPropagation(), (state.mode = (snap.mode + 1) % modes.length))
      }
      onPointerOver={(e) => (e.stopPropagation(), setHovered(true))}
      onPointerOut={(e) => setHovered(false)}
      name={name}
      geometry={(nodes[name] as Mesh).geometry}
      material={(nodes[name] as Mesh).material}
      material-color={snap.current === name ? '#ff6080' : 'white'}
      {...props}
      dispose={null}
    />
  );
}

export type Props = {};

function App01(props: Props) {
  const {} = props;
  const $state = useProxy(state);

  // console.log('App01', { $state });
  return (
    <div className="grid grid-cols-2 gap-4">
      <Area>
        <Canvas camera={{ position: [0, -10, 80], fov: 50 }} dpr={[1, 2]}>
          <pointLight position={[100, 100, 100]} intensity={0.8} />
          <hemisphereLight color="#ffffff" groundColor="#b9b9b9" position={[-7, 25, 13]} intensity={0.85} />
          <React.Suspense>
            <group position={[0, 0, 0]}>
              <Model name="Curly" position={[1, -11, -20]} rotation={[2, 0, -0]} />
              <Model name="DNA" position={[20, 0, -17]} rotation={[1, 1, -2]} />
              <Model name="Headphones" position={[20, 2, 4]} rotation={[1, 0, -1]} />
              <Model name="Notebook" position={[-21, -15, -13]} rotation={[2, 0, 1]} />
              <Model name="Rocket003" position={[18, 15, -25]} rotation={[1, 1, 0]} />
              <Model name="Roundcube001" position={[-25, -4, 5]} rotation={[1, 0, 0]} scale={0.5} />
              <Model name="Table" position={[1, -4, -28]} rotation={[1, 0, -1]} scale={0.5} />
              <Model name="VR_Headset" position={[7, -15, 28]} rotation={[1, 0, -1]} scale={5} />
              <Model name="Zeppelin" position={[-20, 10, 10]} rotation={[3, -1, 3]} scale={0.005} />
            </group>
          </React.Suspense>
        </Canvas>
      </Area>
      <Area className="p-4">
        <ul className="flex flex-col items-start">
          {Array.from($state.models.values()).map((name) => (
            <li key={name} className="flex items-center text-xs">
              <span
                className="w-3 h-3 rounded-full mr-2"
                style={{
                  backgroundColor: $state.current === name ? '#ff6080' : 'white',
                }}
              />
              {name}
            </li>
          ))}
        </ul>
      </Area>
    </div>
  );
}

export default React.memo(App01);
